/*fix menu*/
.menu-item--expanded {
  position: relative;
}

header .menu > .menu-item > a:hover,
header .menu > .menu-item > span:hover {
  color: #219289;
}

header .menu > .menu-item > a,
header .menu > .menu-item > span {
  transition: 0.3s all;
  font-size: 12px;
  line-height: 2;
  color: white;
}

header .menu > .menu-item > span {
  color: #fff;
  text-decoration: none;
  font-size: 12px;
  line-height: 2;
  font-weight: 200;
  cursor: pointer;
}

.menu-item--expanded .menu {
  position: absolute;
  display: flex;
  flex-direction: column;
  min-width: 220px;
  background: rgba(50, 50, 50, 0.7);
  border-top: 1px solid #219289;
  padding: 0 !important;
  z-index: 9999;
  /*    border: 1px solid #fff;*/
}

.menu-item--expanded .menu li.menu-item {
  padding: 0;
}

.menu-item--expanded .menu li.menu-item a,
.menu-item--expanded .menu li.menu-item span {
  padding: 7px 15px;
  display: block;
  position: relative;
}

.menu-item--expanded .menu li.menu-item a:hover,
.menu-item--expanded .menu li.menu-item span:hover {
  background: rgba(33, 146, 137, 0.07);
  font-weight: 700;
  color: #219289;
}

.menu-item--expanded .menu li.menu-item:not(:last-child) {
  border-bottom: 1px solid #f1f1f1;
}

.menu-item--expanded {
  padding-bottom: 16px !important;
}

.menu-item--expanded .menu {
  visibility: hidden;
  height: 0;
  opacity: 0;
  margin: 0 !important;
  transition: 0.3s all;
}

.menu-item--expanded:hover > .menu {
  visibility: visible;
  height: auto;
  opacity: 1;
  margin: 16px 0 !important;
}

.menu-item--expanded .menu .menu-item--expanded {
  padding-bottom: 0 !important;
  position: relative;
}

.menu-item--expanded .menu .menu-item--expanded:after {
  content: "\f104";
  font-family: "Font Awesome 5 Free";
  -webkit-font-smoothing: antialiased;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  line-height: 1;
  font-variant: normal;
  font-weight: 900;
  position: absolute;
  left: 10px;
  top: 34%;
  color: #717580;
}

.header-logo img {
  width: 125px;
}

header .block .content {
  margin-top: 25px;
}

.menu-item--expanded .menu .menu-item--expanded > .menu {
  left: -101%;
  top: -17px;
}

.menu-item--expanded .menu .menu-item--expanded:hover:after {
  content: "\f105";
}

