*/
.btn-breadcrumb .btn {
  transition: 0.3s all;
}

.btn-breadcrumb .btn:after {
  content: " ";
  display: block;
  width: 0;
  height: 0;
  border-top: 17px solid transparent;
  border-bottom: 17px solid transparent;
  border-right: 10px solid #f3f5fb;
  position: absolute;
  top: 50%;
  margin-top: -17px;
  right: 100%;
  z-index: 3;
}

.btn-breadcrumb .btn:before {
  content: " ";
  display: block;
  width: 0;
  height: 0;
  border-top: 17px solid transparent;
  border-bottom: 17px solid transparent;
  border-right: 10px solid #adadad;
  position: absolute;
  top: 50%;
  margin-top: -17px;
  margin-right: 0px;
  right: 100%;
  z-index: 3;
}

.btn-default {
  color: #747882 !important;
  background-color: #f3f5fb;
  border-color: #e8edf2;
  border: 0 !important;
}

.btn-default:hover {
  border: 1px solid transparent;
}

.btn-group .btn + .btn {
  margin-right: 0px;
}

.btn-breadcrumb .btn {
  padding: 6px 24px 6px 12px;
}

.btn-breadcrumb .btn:first-child {
  padding: 6px 10px 6px 6px;
}

.btn-breadcrumb .btn:last-child {
  padding: 6px 24px 6px 18px;
}

/** Default button **/
.btn-breadcrumb .btn.btn-default:after {
  border-right: 10px solid #f3f5fb;
}

.btn-breadcrumb .btn.btn-default:before {
  border-right: 10px solid #9b9b9b;
}

.btn-breadcrumb .btn.btn-default:hover:after {
  border-right: 10px solid #f3f5fb;
}

.btn-breadcrumb .btn.btn-default:hover:before {
  border-right: 10px solid #e8edf2;
}

.btn-group .btn:last-child {
  margin-right: 0 !important;
}

a.btn.btn-default.btn-active {
  background: #219289;
  color: white !important;
  border-color: #219289;
}

.btn-breadcrumb a.btn.btn-default.btn-active:after {
  border-right: 10px solid #219289;
}

.btn-breadcrumb a.btn.btn-default.btn-active:before {
  border-right: 10px solid #219289;
}

.btn-breadcrumb a.btn.btn-default.btn-active:hover:after {
